import React, { Component } from 'react';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import SVG from 'react-inlinesvg';
class DisplayOnsiteStrip extends Component {
    render() {
        const { onSiteUpload  } = this.props.templateFormData
        const ButtonBgStyle = {
            fontFamily: onSiteUpload.onsite_css_font_btn,
            backgroundColor: onSiteUpload.bgColor
        }
      
        const QRButtonText = onSiteUpload.qrCodeStatus || onSiteUpload.buttonText != '' ? onSiteUpload.buttonText : ''
      
        const buttonStyle = {
            fontFamily: onSiteUpload.fontFamily.family,
            color: onSiteUpload.textColor,
            fontSize: onSiteUpload.fontSize+'px',
            textAlign: onSiteUpload.qrCodeStatus === 1 ? 'left' : 'center'
        }
        return  onSiteUpload.buttonStatus && (onSiteUpload.qrCodeStatus == 1 || QRButtonText !== '') ?  <div className="tb_dp_onsite_upload_btn_wrap">
                        <div style={ButtonBgStyle} className='tb_dp_onsite_inner'>
                            <div className="tb_dp_onsite_qr_btn_wrap">
                            {onSiteUpload.qrCodeStatus == 1 ? <div className='tb_dp_onsite_qr_code'>
                                <SVG src={baseUrl('media/icons/svg-icons/qr-code.svg')} height={60} width={60} />
                            </div> : null}
                            {QRButtonText !== '' ?
                                <div className={`tb_dp_onsite_btn_txt tb-cTBfont-${onSiteUpload.fontVarient}`} style={buttonStyle}>
                                {QRButtonText}
                                </div> : ''}
                            </div>
                        </div>
                </div>:null
    }
}

export default DisplayOnsiteStrip;